// Import font
@font-face {
    font-family: 'iAWriter';
    src: url('fonts/iAWriterDuospace-Regular.woff2') format('woff2');
    font-weight: 400;
    font-style: normal;
}

@keyframes pageLoad {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@mixin main_anim($delay) {
    opacity: 0;
    animation: pageLoad 1.2s ease-in-out $delay forwards;
}
// Variables
$fg-color: #e6e0d8;
$bg-color: #1a1918;

$font-body: "iA Writer Duospace", monospcae;


// defaults
body {
    color: var(--fg-color);
    background-color: var(--bg-color);
    font: 0.8750em $font-body;
    cursor: default;
    -moz-font-feature-settings: "ss04";
    -webkit-font-feature-settings: "ss04";
    font-feature-settings: "ss04";
}

a, a:visited {
    &:hover::before { content: "> "; }
    text-decoration: none;
    color: var(--fg-color);
    cursor: pointer;
}
button {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	outline: inherit;
    cursor: pointer;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80vw;
    height: 80vh;
}
// body class
.body {
    position: relative;
    width: 450px;
    height: 470px;
    line-height: 175%;
}

//metadata class
.metadata{
    @include main_anim(0s);
    text-transform: lowercase;
}

.about {
    @include main_anim(.4s);
    text-align: justify;
}
.portfolio {
    @include main_anim(.8s);
    text-align: justify;
}
.links {
    @include main_anim(1.2s);
    width: 100px;
    height: 120px;
}

//PORTFOLIO TABLE
$delay-counter: 0s;
@for $i from 1 through 6 {
    .w#{$i} {
        @include main_anim($delay-counter);
    }
    $delay-counter: $delay-counter + .4;
}
.right {
    float: right;
}
